.feedback-form {
  @media (min-width: 768px) {
    padding: 0 50px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 80px;
  }

  @media (min-width: 1200px) {
    padding: 0 100px;
    column-gap: 100px;
  }
}

.feedback-form__phone {
  @media (min-width: 1200px) {
    display: none;
  }
}

.feedback-form__field {
  background-color: var(--background);
  padding: 14px 22px;
  box-sizing: border-box;
  border: 4px solid var(--background);
  font-family: "PT Sans", "Arial", sans-serif;
  font-weight: bold;
  font-size: 18px;
  line-height: 26px;

  &:hover {
    background-color: var(--gray);
    border: 4px solid var(--gray);
    transition: 300ms ease-out;
  }

  &:active {
    background-color: var(--white);
    transition: 300ms ease-out;
  }

  &:focus {
    outline: 0;
    background-color: var(--white);
    transition: 300ms ease-out;
  }

  &:disabled {
    opacity: 0.3;
  }
}

.feedback-form__field::placeholder {
  color: var(--black);
  opacity: 1;
}

.feedback-form__title {
  font-size: 18px;
  line-height: 26px;
  font-weight: bold;
  margin: 0 auto 19px auto;
  text-align: center;

  @media (min-width: 768px) {
    font-size: 21px;
  }
}

.feedback-form__title--message {
  margin-bottom: 7px;

  @media (min-width: 768px) {
    margin-bottom: 23px;
  }

  @media (min-width: 1200px) {
    margin-top: 28px;
  }
}

.feedback-form__label {
  font-size: 14px;
  line-height: 26px;
  width: 100%;
}

.feedback-form__label--name {
  display: flex;
  justify-content: space-between;
  align-items: baseline;
  width: 100%;
}

.feedback-form__field--name {
  width: 100%;
  margin-left: 10px;
  height: 53px;
  box-sizing: border-box;
}

.feedback-form__label--radio,
.feedback-form__label--checkbox {
  font-size: 18px;
  line-height: 26px;
  font-weight: bold;
  text-transform: none;
  padding: 15px 0 15px 65px;
  position: relative;
  max-width: 190px;
  cursor: pointer;

  &:disabled {
    color: var(--lightest-gray);
  }

  @media (min-width: 768px) {
    padding: 7px 0 7px 85px;
  }
}

.feedback-form__radio,
.feedback-form__checkbox {
  &:disabled {
    opacity: 0.3;
  }
}

.feedback-form__label--radio::before {
  content: "";
  position: absolute;
  width: 56px;
  height: 56px;
  border-radius: 50%;
  top: -2px;
  left: -12px;
  background-color: var(--beige);
  background-repeat: no-repeat;
  background-position: 0 0;
  cursor: pointer;

  @media (min-width: 768px) {
    top: -10px;
    left: 10px;
  }
}

.feedback-form__radio:checked + label::before {
  background-color: var(--dark);
  width: 20px;
  height: 20px;
  border: 18px solid var(--beige);
}

.feedback-form__label--radio:hover::before {
  background-color: var(--gray);
}

.feedback-form__radio:focus + label::before {
  border: 18px solid var(--darkest-gray);
}

.feedback-form__label--radio:active::before {
  background-color: var(--darkest-gray);
}

.feedback-form__radio:active:checked + label::before {
  background-color: var(--gray-shadow);
  width: 20px;
  height: 20px;
  border: 18px solid var(--darkest-gray);
}

.feedback-form__label--checkbox::before {
  content: "";
  position: absolute;
  width: 55px;
  height: 55px;
  top: 0;
  left: -15px;
  background-color: var(--beige);
  background-repeat: no-repeat;
  background-position: 0 0;
  border-radius: 6px;
  cursor: pointer;

  @media (min-width: 1200px) {
    top: 0;
    left: -5px;
  }
}

.feedback-form__checkbox:checked + label::before {
  content: "";
  position: absolute;
  width: 55px;
  height: 55px;
  background-color: var(--beige);
  background-repeat: no-repeat;
  background-position: 0 0;
  border-radius: 6px;
  cursor: pointer;
  z-index: 0;
}

.feedback-form__checkbox:checked + label::after {
  content: "";
  position: absolute;
  transform: rotate(-45deg);
  height: 11px;
  width: 20px;
  left: 0;
  top: 15px;
  border-radius: 2px;
  border-bottom: 4px solid var(--black);
  border-left: 4px solid var(--black);

  @media (min-width: 1200px) {
    left: 10px;
  }
}

.feedback-form__label--checkbox:hover::before {
  background-color: var(--gray);
}

.feedback-form__checkbox:focus + label::before {
  background-color: var(--darkest-gray);
}

.feedback-form__checkbox:active:checked + label::before,
.feedback-form__checkbox:focus:checked + label::before {
  background-color: var(--darkest-gray);
}

.feedback-form__checkbox:active:checked + label::after {
  content: "";
  position: absolute;
  transform: rotate(-45deg);
  height: 11px;
  width: 20px;
  left: 0;
  top: 15px;
  border-radius: 2px;
  border-bottom: 4px solid var(--gray-shadow);
  border-left: 4px solid var(--gray-shadow);
}

.feedback-form__label--checkbox {
  margin-left: 15px;
  padding-right: 0;

  @media (min-width: 768px) {
    margin: 0;
    width: 215px;
  }

  @media (min-width: 1200px) {
    width: 15%;
  }
}

.feedback-form__label--contact {
  @media (min-width: 1200px) {
    display: flex;
    justify-content: flex-start;
    align-items: baseline;
    width: 470px;
  }
}

.feedback-form__field--phone,
.feedback-form__field--email {
  margin-top: 8px;
  width: 100%;
  min-height: 58px;
  box-sizing: border-box;
  padding-right: 70px;
  padding-left: 20px;

  @media (min-width: 768px) {
    margin-top: 10px;
    margin-bottom: 10px;
    padding-left: 19px;
  }

  @media (min-width: 1200px) {
    width: 315px;
    margin-left: 10px;
    padding-left: 24px;
  }
}

.feedback-form__field--email {
  margin-top: 6px;

  @media (min-width: 1200px) {
    margin-left: 8px;
    padding-left: 30px;
  }
}

.feedback-form__label--phone,
.feedback-form__label--email {
  position: relative;
}

.feedback-form__label--phone {
  @media (min-width: 1200px) {
    padding-left: 20px;
  }
}

.feedback-form__field-icon--phone {
  content: "";
  position: absolute;
  width: 62px;
  height: 62px;
  right: 0;
  top: 32px;

  @media (min-width: 768px) {
    top: 36px;
  }

  @media (min-width: 1200px) {
    margin-left: 15px;
    top: 10px;
    right: 69px;
  }
}

.feedback-form__field-icon--email {
  content: "";
  position: absolute;
  width: 62px;
  height: 62px;
  right: 0;
  top: 32px;

  @media (min-width: 768px) {
    top: 36px;
  }

  @media (min-width: 1200px) {
    margin-left: 15px;
    top: 6px;
    right: -9px;
  }
}

.feedback-form__message {
  border: none;
  background-color: var(--background);
  resize: none;
  width: 100%;
  height: 160px;
  margin-bottom: 58px;
  padding: 20px;
  box-sizing: border-box;
  font-family: "PT Sans", "Arial", sans-serif;
  font-size: 18px;
  line-height: 26px;
  font-weight: bold;

  &:focus {
    outline: 0;
    background-color: var(--white);
    border: 4px solid var(--gray);
    padding: 16px;
  }

  &:disabled {
    opacity: 0.3;
  }

  @media (min-width: 768px) {
    width: 97%;
    margin-top: 30px;
    margin-bottom: 59px;
  }

  @media (min-width: 1200px) {
    padding-left: 25px;
    margin-bottom: 58px;
  }
}

.feedback-form textarea::placeholder {
  color: var(--black);
  opacity: 1;
  font-family: "PT Sans", "Arial", sans-serif;
  font-weight: bold;
  font-size: 18px;
  line-height: 26px;
}

.feedback-form__button {
  @include button;
  font-family: "PT Sans", "Arial", sans-serif;
  font-size: 21px;
  line-height: 26px;
  font-weight: bold;
  padding-top: 18px;
  padding-bottom: 17px;
  min-width: 280px;
  max-width: 580px;

  @media (min-width: 768px) {
    min-width: 324px;
    margin-left: 10px;
    padding-left: 0;
  }

  @media (min-width: 1200px) {
    min-width: 480px;
    margin-left: 20px;
  }
}

.feedback-form__footer {
  @media (min-width: 768px) {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
  }
}

.feedback-form__footnote {
  display: none;

  @media (min-width: 768px) {
    display: inline;
    font-size: 14px;
    line-height: 26px;
    margin-right: 10px;
    margin-top: 20px;
  }

  @media (min-width: 1200px) {
    margin-right: 20px;
  }
}
