@keyframes bounce {
  0% {
    transform: translateY(-2000px);
  }
  70% {
    transform: translateY(30px);
  }
  90% {
    transform: translateY(-10px);
  }
  100% {
    transform: translateY(0);
  }
}
@keyframes slide {
  0% {
    transform: translateY(-2000px);
  }
  100% {
    transform: translateY(0);
  }
}
@keyframes shake {
  0%, 100% {
    transform: translateX(0);
  }
  10%, 30%, 50%, 70%, 90% {
    transform: translateX(-10px);
  }
  20%, 40%, 60%, 80% {
    transform: translateX(10px);
  }
}
@font-face {
  font-family: "PT Sans";
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "PT Sans";
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
:root {
  --blue: #81b3d2;
  --dark-blue: #669ec0;
  --darkest-blue: #5496bd;
  --black: #000000;
  --white: #ffffff;
  --white-transparent: rgba(255, 255, 255, 0.5);
  --background: #eeeeee;
  --dark-shadow: #658da5;
  --gray-shadow: #acabab;
  --gray: #e5e5e5;
  --dark-gray: #5c6870;
  --darkest-gray: #d5d5d5;
  --light-shadow: #91c6e7;
  --special: #766357;
  --page-back: #e7e5e5;
  --beige: #f2f2f2;
  --black-transparent: rgba(0, 0, 0, 0.5);
  --dark: #333333;
  --transparent: rgba(255, 255, 255, 0);
}

.visually-hidden {
