  transition: 300ms ease-out;
}
.copyright__icon:active {
  fill: var(--black);
  opacity: 0.3;
  transition: 300ms ease-out;
}

.copyright__text {
  font-size: 14px;
  line-height: 26px;
  margin-right: 8px;
}

@media (min-width: 768px) {
  .feedback-form {
    padding: 0 50px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 80px;
  }
}
@media (min-width: 1200px) {
  .feedback-form {
    padding: 0 100px;
    column-gap: 100px;
  }
}

@media (min-width: 1200px) {
  .feedback-form__phone {
    display: none;
  }
}

.feedback-form__field {
  background-color: var(--background);
  padding: 14px 22px;
  box-sizing: border-box;
  border: 4px solid var(--background);
  font-family: "PT Sans", "Arial", sans-serif;
  font-weight: bold;
  font-size: 18px;
  line-height: 26px;
}
.feedback-form__field:hover {
  background-color: var(--gray);
  border: 4px solid var(--gray);
  transition: 300ms ease-out;
}
.feedback-form__field:active {
  background-color: var(--white);
  transition: 300ms ease-out;
}
.feedback-form__field:focus {
  outline: 0;
  background-color: var(--white);
  transition: 300ms ease-out;
}
.feedback-form__field:disabled {
  opacity: 0.3;
}

.feedback-form__field::placeholder {
  color: var(--black);
  opacity: 1;
}

.feedback-form__title {
  font-size: 18px;
  line-height: 26px;
  font-weight: bold;
  margin: 0 auto 19px auto;
  text-align: center;
}
@media (min-width: 768px) {
  .feedback-form__title {
    font-size: 21px;
  }
}

.feedback-form__title--message {
  margin-bottom: 7px;
}
@media (min-width: 768px) {
  .feedback-form__title--message {
    margin-bottom: 23px;
  }
}
@media (min-width: 1200px) {
  .feedback-form__title--message {
    margin-top: 28px;
  }
}

.feedback-form__label {
  font-size: 14px;
  line-height: 26px;
  width: 100%;
}

.feedback-form__label--name {
  display: flex;
  justify-content: space-between;
  align-items: baseline;
  width: 100%;
}

.feedback-form__field--name {
  width: 100%;
  margin-left: 10px;
  height: 53px;
  box-sizing: border-box;
}

.feedback-form__label--radio,
.feedback-form__label--checkbox {
  font-size: 18px;
  line-height: 26px;
  font-weight: bold;
  text-transform: none;
  padding: 15px 0 15px 65px;
  position: relative;
  max-width: 190px;
  cursor: pointer;
}
.feedback-form__label--radio:disabled,
.feedback-form__label--checkbox:disabled {
  color: var(--lightest-gray);
}
@media (min-width: 768px) {
  .feedback-form__label--radio,
.feedback-form__label--checkbox {
    padding: 7px 0 7px 85px;
  }
}

.feedback-form__radio:disabled,
.feedback-form__checkbox:disabled {
  opacity: 0.3;
}

.feedback-form__label--radio::before {
  content: "";
  position: absolute;
  width: 56px;
  height: 56px;
  border-radius: 50%;
  top: -2px;
  left: -12px;
  background-color: var(--beige);
  background-repeat: no-repeat;
  background-position: 0 0;
  cursor: pointer;
}
@media (min-width: 768px) {
  .feedback-form__label--radio::before {
    top: -10px;
    left: 10px;
  }
}

.feedback-form__radio:checked + label::before {
  background-color: var(--dark);
  width: 20px;
  height: 20px;
  border: 18px solid var(--beige);
}

.feedback-form__label--radio:hover::before {
  background-color: var(--gray);
}

.feedback-form__radio:focus + label::before {
  border: 18px solid var(--darkest-gray);
}

.feedback-form__label--radio:active::before {
  background-color: var(--darkest-gray);
}

.feedback-form__radio:active:checked + label::before {
  background-color: var(--gray-shadow);
  width: 20px;
  height: 20px;
  border: 18px solid var(--darkest-gray);
}

.feedback-form__label--checkbox::before {
  content: "";
  position: absolute;
  width: 55px;
  height: 55px;
  top: 0;
  left: -15px;
  background-color: var(--beige);
  background-repeat: no-repeat;
  background-position: 0 0;
  border-radius: 6px;
  cursor: pointer;
}
@media (min-width: 1200px) {
  .feedback-form__label--checkbox::before {
    top: 0;
    left: -5px;
  }
}

.feedback-form__checkbox:checked + label::before {
  content: "";
  position: absolute;
  width: 55px;
  height: 55px;
  background-color: var(--beige);
  background-repeat: no-repeat;
  background-position: 0 0;
